const data = {
  apiKey: 'AIzaSyDz-1hA0zNH0EHgoOjNBsmvlsEI3TS9jrQ',
  clientId: '18688042344-qlttf3r6hsajjk2oqpr12gqflmumks2s.apps.googleusercontent.com',
  scope: 'https://www.googleapis.com/auth/script.projects https://www.googleapis.com/auth/script.external_request',
  discoveryDocs: ['https://script.googleapis.com/$discovery/rest?version=v1']
};

class API {

  gapi;
  googleAuth;
  isAuthorized = false;
  user = null;
  scriptId = 'M1qUJ-bECOgm4Y9z9evwE5gNpR_a-QLCI';

  constructor (data) {
    let init = () => {
      this.gapi = window.gapi;
      this.gapi.load('client:auth2', load);
    };
    let load = () => {
      this.gapi.client
        .init(data)
        .then(setAuth);
    };
    let setAuth = () => {
      this.googleAuth = this.gapi.auth2.getAuthInstance();
      this.googleAuth.isSignedIn.listen(updateUser);
    };
    let updateUser = (isSignedIn) => {
      if (isSignedIn) {
        this.isAuthorized = true;
        this.user = this.googleAuth.currentUser.get();
      } else {
        this.isAuthorized = false;
        this.user = null;
      }
    };
    if (window.gapi) {
      init();
    } else {
      window.addEventListener('gapiLoaded', init);
    }
  }

  authorize () {
    if (this.isAuthorized) {
      this.googleAuth.signOut();
    } else {
      this.googleAuth.signIn();
    }
  }

}

export default new API(data);